/* CSS Resets */
*, 
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Open Sans', sans-serif;
}

/* Variables */
:root {
  --color-white: #fff;
  --color-light-white: #eee;
  
  --color-tomato: #d40640;
  --color-purple-transparent: rgba(88, 24, 69, .9);
  --color-pink-transparent: rgba(216, 14, 91, .9);
  
  --color-orange: #ff3c00;
  
  --color-light-black: #222;
  --color-light-black: #000;
  
  --shadow: rgba(0, 0, 0 , .4);
}

.app {
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(to bottom right, var(--color-pink-transparent), var(--color-purple-transparent)), url(https://i.pinimg.com/originals/91/b5/cd/91b5cdab51e207263169904b227503b4.jpg);
  background-size: cover;

  display: flex;
  justify-content: center;
  align-items: center;
}

.calculator {
  height: 80vh;
  width: 80%;
  box-shadow: 0 2rem 4rem var(--shadow);
  background: var(--color-light-white);
}

.screen {
  height: 31.25%;
  width: 100%;
  padding: 0 2rem;
  /* overflow: hidden; */
  /* white-space: nowrap; */
  color: var(--color-light-white);
  background-color: var(--color-tomato);
}

.result-screen {
  height: 64%;
  font-size: 5rem;
  font-weight: 600;
  /* To display the value at the bottom-right of the container */
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.computation-screen {
  height: 36%;
  font-size: 2rem;
  /* To display the value at the bottom-right of the container */
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}


.keypad {
  height: 68.75%;
  width: 100%;
  color: white;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
}

.btn {
  font-size: 1.8rem;  
  font-family: inherit;
  background-color: var(--color-white);
  border: 1px solid transparent;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .2s;
}

.btn:hover {
  color: var(--color-light-white);
  background: var(--color-tomato);
}

.btn:active {
  transform: scale(.95);
}

.btn--large {
  grid-column: -3 / -1;
  color: var(--color-white);
  background-color: var(--color-tomato);
  transition: all .2s;
}

.btn--large:hover {
  background-color: var(--color-white);
  color: var(--color-tomato);
}